import React, { useState, useEffect } from 'react';
import Pagination from './Pagination/Pagination';
import { Pockemons } from '../../API/GET/pockemon';
import { PockemonFirstPage } from '../../Utils/ConstValues';
const Pokemon = () => {
    const pageLimit = 20;
    const [pokemon, setPokemon] = useState([]);
    const [currentPageUrl, setCurrentPageUrl] = useState(PockemonFirstPage);
    const [totalPokemon, setTotalPockemon] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [nextPageUrl, setNextPageUrl] = useState();
    const [prevPageUrl, setPrevPageUrl] = useState();
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setLoading(true)
        Pockemons(currentPageUrl)
            .then(data => {
                setTotalPockemon(data.count);
                setLoading(false);
                setNextPageUrl(data.next);
                setPrevPageUrl(data.previous);
                setPokemon(data.results);
            })
    }, [currentPageUrl])
    const onPageChanged = data => {
        const { currentPage } = data;

        setCurrentPage(currentPage);
    };
    const gotoNextPage = () => {
        setCurrentPageUrl(nextPageUrl)
    }

    const gotoPrevPage = () => {
        setCurrentPageUrl(prevPageUrl)
    }
    if (loading) return "Loading..."
    return (
        <div className="flex">
            {
                totalPokemon > 0 ?
                    <Pagination
                        currentPage={currentPage}
                        totalRecords={totalPokemon}
                        pageLimit={pageLimit}
                        gotoNextPage={nextPageUrl ?  () => gotoNextPage() : null}
                        gotoPrevPage={prevPageUrl ? () => gotoPrevPage() : null}
                        pageNeighbours={1}
                        onPageChanged={(data) => onPageChanged(data)} />
                    : null
            }
        </div>
    )
}

export default Pokemon;